const privilege = ["所有人可见","仅好友可见","仅自己可见"];

import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue'
import uploadImage from '../../components/publish/upload-image.vue'
export default {
	components:{
		uniNavBar,
		uploadImage
	},
	data() {
		return {
			yinsi : "所有人可见"
		}
	},
	methods: {
		
		// 返回上一级
		back(){
			uni.navigateBack({
				delta: 1
			});
		},
		// 发布说说
		publish(){
			console.log("发布");
		},
		// 改变说说权限
		changTitle(){
			uni.showActionSheet({
				itemList : privilege,
			    success:(res)=> {
					this.yinsi = privilege[res.tapIndex]
			    }
			});
		}
	}
}
